.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  height: 35px;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.info-item {
  text-align: center;
  margin-right: 10px;
}

.form-class {
  height: 50px;
  background-color: #3f56b1;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

input {
  height: 30px;
}

button {
  height: 30px;
  border-radius: 0;
}

.button-load {
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.load-more {
  color: #fff;
  background-color: #3f51b5;
}

.load-more:hover {
  background-color: #192665;
}

